import "./Sales.css";
import axios from "axios";
import { useEffect, useState } from "react";
import config from "../../../Configs/Configs";
import productModel from "../../../Modules/productModel";
import setHeaders from "../../../Services/setHeaders";
import RecipeReviewCard from "../../Product/UseCard/UseCard";
function Sales(): JSX.Element {
    const [product ,setProduct] = useState<productModel[]>();
    const headers = setHeaders();
    useEffect(()=>{
        axios.get<productModel[]>(config.urls.product + "sales/",{headers:headers}).then((res)=>{
            setProduct(res.data)}).catch((e)=>{console.log(e)})
        
    },[])
    return (
        <div className="Sales">
			{
                product?.map(p=><RecipeReviewCard product={p} key ={p.id}/>)
            }
        </div>
    );
}

export default Sales;
