import axios from "axios";
import { Component, ReactNode, useEffect, useState } from "react";
import { NavLink, RouteComponentProps, useHistory } from "react-router-dom";
import config from "../../../Configs/Configs";
import productModel from "../../../Modules/productModel";
import "./Buy.css";
import setHeaders from "../../../Services/setHeaders";
import Similar from "../Similar/Similar";
import Sales from "../../Home/Sales/Sales";
import Grid from "@mui/material/Grid";
import { Button, Typography } from "@mui/material";
interface RouterParams {
    image: string;
    id: string;
}
interface BuyProps extends RouteComponentProps<RouterParams> {
}
export default function Buy(props:BuyProps){
        const [product ,setProduct] = useState<productModel | null>()
        const history = useHistory()
        useEffect(()=>{
            axios.get(config.urls.product + props.match.params.id)
            .then((res)=>{setProduct(res.data)})
            .catch((err)=>{alert(err)})
        },[]) 
        const renderToCart=()=>{axios.put(config.urls.cart +"add/" + product?.id)}
        const renderToCheckout = ()=>{history.push('/checkout/'+product?.id)}   
        return (
            <Grid container className="Buy" sx={{marginTop:'20px'}}>
              
                <Grid item xs={12} sx={{ width: '100%', height: '320px', textAlign: 'center' }} className='images'>
                    <Typography sx={{ fontFamily: 'Arial', fontSize: '20px' }}>
                        {product?.name}
                    </Typography>
                    <img src={config.urls.product + "images/" + props.match.params.image} />
                    <img src={config.urls.product + "images/" + props.match.params.image} className="NoDisplay" />
                    <img src={config.urls.product + "images/" + props.match.params.image} className="NoDisplay" />
                </Grid>
                <Grid container sx={{height: '150px',width:'100%', display: 'flex', justifyContent: 'center',justifyItems:'center' }}>
                    <Grid item xs={6} sx ={{marginTop:'10px'}}>
                        <Typography sx={{ fontFamily: 'Arial', fontSize: '20px', textAlign: 'center' }}>מחיר</Typography>
                    </Grid>
                    <Grid item xs={6} sx ={{marginTop:'10px'}}>
                        <Typography sx={{ fontFamily: 'Arial', fontSize: '20px', textAlign: 'center' }}>{product?.price}שח</Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <Typography sx={{fontSize:'13px',fontFamily:'Arial, Helvetica, sans-serif'}}>תיאור מוצר</Typography>
                    </Grid>
                    <Grid item xs={12 }>
                    <Typography sx={{fontFamily:'Arial, Helvetica, sans-serif'}}>{product?.description}</Typography>
                    </Grid>
                    <Grid item xs={12} sx={{textAlign:'center',height:'50px'}}>
                     <Button onClick={renderToCheckout} sx={{background:'blue' ,color:'white' ,margin:'10px'}}> רכישה</Button>
                     <Button onClick={renderToCart} sx={{background:'#FAE5D3' ,color:'white' ,margin:'10px'}}> הוסף לסל</Button>
                    </Grid>

                </Grid>
                <Grid>
                    <Typography>אולי יעניינו אותך?</Typography>
                    <Sales />
                </Grid>
            </Grid>
        );
    }


